@charset "UTF-8";
@import "../../../style/theme/default/var.css";

.padding-form {
  padding-bottom: var(--theme-padding-small);
  .el-button+.el-button {
    margin-left: var(--theme-margin-base);
  }
}

@component-namespace rfm {
  @b instructions {
    display: none;
    padding: var(--theme-padding-base);
    margin-bottom: var(--theme-margin-base);
    background-color: var(--theme-background-color-primary);
    p {
      margin-bottom: 0;
    }
    p:not(:last-child) {
      margin-bottom: 5px;
    }
  }
  @b form {
    padding: var(--theme-padding-base) var(--theme-padding-base) var(--theme-padding-small);
    margin-bottom: var(--theme-margin-base);
    background-color: var(--theme-background-color-primary);
  }
}


@component-namespace analysis{
  @b panel {
    padding: 0 var(--theme-padding-base) var(--theme-padding-base);
    background-color: var(--color-white);
    border: 1px solid var(--border-color-base);
    margin-bottom: var(--theme-margin-base);
    @e title{
      height:50px;
      line-height: 30px;
      padding: var(--theme-padding-base) 0;
      & > span{
        position: relative;
        display: inline-block;
        padding:0 var(--theme-padding-small);
        &:before {
           content: ' ';
           position: absolute;
           top: -10px;
           left: 5px;
           right: 5px;
           height: 4px;
           background-color: var(--theme-color-primary);
         }
      }
    }
    @e body {
      .salesTrend-echart {
        width: 99%;
      }
    }
  }
  @b form {
    padding-top: var(--theme-padding-large);
    .spacing-button{
      line-height: 20px;
      display:inline-block;
      .el-radio-button {
        .el-radio-button__inner {
          padding: 8px 20px;
          color: var(--font-color-base);
          border: 1px solid var(--theme-border-color-primary);
          background-color: transparent;
        }
      }
      .el-radio-button__orig-radio:checked + .el-radio-button__inner {
        color: var(--color-white);
        border-color: var(--theme-color-primary);
        background-color: var(--theme-color-primary);
      }
    }
  }
}

